@keyframes appear{
    0% {
        opacity: 0;
        top: 35px;
    }

    100% {
        opacity: 1;
        top: 0;
    }
}

.wrapper {
    list-style: none;
    padding: 0px 30px 60px 0;
    display: flex;
    align-items: center;
    position: relative;
    animation: appear .5s ease   
}

.image {
    flex-shrink: 0;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-right: 40px;
}

.imageNone { 
    @extend .image;
    background: url('https://unsplash.it/200/200');
    background-repeat: no-repeat;
    background-size: cover;
}

.description {
    margin: 10px 0 20px;
    font-weight: 300;
}

